import { Sequelize, DataTypes } from 'sequelize'
import moment from 'moment'
import sequelize from './sequelize'

// 应聘信息
const YingpinxinxiModel = sequelize.define('YingpinxinxiModel', {
	id: {
		type: DataTypes.BIGINT,
		primaryKey: true,
		autoIncrement: true,
		allowNull: false,
		comment: '主键id'
	},
	gangweimingcheng: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '岗位名称'
	},
	tupian: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '图片'
	},
	zhiweileibie: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '职位类别'
	},
	gongzidaiyu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '工资待遇'
	},
	shangbanshijian: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '上班时间'
	},
	nianlingyaoqiu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '年龄要求'
	},
	xueliyaoqiu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学历要求'
	},
	qiyezhanghao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业账号'
	},
	qiyemingcheng: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业名称'
	},
	gerenjianli: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '个人简历'
	},
	qiuzhishijian: {
		type: DataTypes.DATE,
		allowNull: true,
		get() {
            return moment(this.getDataValue('qiuzhishijian')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '求职时间'
	},
	beizhu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '备注'
	},
	xuehao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学号'
	},
	xueshengxingming: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学生姓名'
	},
	shoujihaoma: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '手机号码'
	},
	crossuserid: {
		type: DataTypes.BIGINT,
		defaultValue: 0,
		allowNull: true,
		comment: '跨表用户id'
	},
	crossrefid: {
		type: DataTypes.BIGINT,
		defaultValue: 0,
		allowNull: true,
		comment: '跨表主键id'
	},
	sfsh: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '是否审核'
	},
	shhf: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '审核回复'
	},
	userid: {
		type: DataTypes.BIGINT,
		defaultValue: 0,
		allowNull: true,
		comment: '用户id'
	},
	addtime: {
  		type: DataTypes.DATE,
  		defaultValue: DataTypes.NOW,
    	allowNull: false,
    	get() {
            return moment(this.getDataValue('addtime')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '添加时间'
	}
}, {
	timestamps: false,
	freezeTableName: true,
	tableName: 'yingpinxinxi'
})

export default YingpinxinxiModel
